import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import { paramCase } from 'change-case';
import { useRef, useState } from 'react';
import editFill from '@iconify/icons-eva/edit-fill';
import { Link as RouterLink } from 'react-router-dom';
import trash2Outline from '@iconify/icons-eva/trash-2-outline';
import unlockFill from '@iconify/icons-eva/unlock-fill';
import lockFill from '@iconify/icons-eva/lock-fill';
import moreVerticalFill from '@iconify/icons-eva/more-vertical-fill';
import { useSnackbar } from 'notistack';
// material
import { Menu, MenuItem, IconButton, ListItemIcon, ListItemText } from '@mui/material';
import Request from '../../../../utils/Request';
import { useDispatch, useSelector } from '../../../../redux/store';
import { getBoules, getStatistiques } from '../../../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../../../routes/paths';
import { PERMISSIONS } from '../../../../guards/Permissions';
import useAuth from '../../../../hooks/useAuth';
import { handlePermission } from '../../../../utils/handlePermission';
// ----------------------------------------------------------------------

BouleMoreMenu.propTypes = {
  bouleId: PropTypes.number,
  boule: PropTypes.number,
  block: PropTypes.bool,
  handleGo: PropTypes.func
};

export default function BouleMoreMenu({ bouleId, boule, block, handleGo }) {
  const ref = useRef(null);
  const { user } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();

  const handleBlock = async () => {
    setIsOpen(false);
    const values = {
      bouleId,
      boule
    };
    try {
      await Request.handleModifyBouleBlock(values);
      dispatch(getBoules());
      handleGo();
      enqueueSnackbar(block ? 'Unblock avec succès' : 'Block avec succès', { variant: 'success' });
    } catch (e) {
      enqueueSnackbar('An error occured!', { variant: 'error' });
    }
  };

  return (
    <>
      <IconButton ref={ref} onClick={() => setIsOpen(true)}>
        <Icon icon={moreVerticalFill} width={20} height={20} />
      </IconButton>

      <Menu
        open={isOpen}
        anchorEl={ref.current}
        onClose={() => setIsOpen(false)}
        PaperProps={{
          sx: { width: 200, maxWidth: '100%' }
        }}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        {handlePermission(user?.perms, PERMISSIONS.BLOCK_BOULE) && (
          <MenuItem onClick={handleBlock} sx={{ color: block ? 'green' : 'red' }}>
            <ListItemIcon>
              <Icon icon={trash2Outline} width={24} height={24} />
            </ListItemIcon>
            <ListItemText primary={block ? 'Unblock' : 'Block'} primaryTypographyProps={{ variant: 'body2' }} />
          </MenuItem>
        )}
      </Menu>
    </>
  );
}
