import { filter } from 'lodash';
import { Icon } from '@iconify/react';
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink } from 'react-router-dom';
// material
import { useSnackbar } from 'notistack';
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Stack,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination
} from '@mui/material';
import Request from '../../utils/Request';
import { fDateTime } from '../../utils/formatTime';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getBoules } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { BouleListHead, BouleListToolbar, BouleMoreMenu } from '../../components/_dashboard/boule/list';
import { handlePermission } from '../../utils/handlePermission';
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'tirage', label: 'Tirage', alignRight: false },
  { id: 'agent', label: 'Agent', alignRight: false },
  { id: 'boule', label: 'Boule', alignRight: false },
  { id: 'date', label: 'Date', alignRight: false },
  { id: '' }
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_boule) => _boule?.boule.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function Boule() {
  const { user } = useAuth();
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const { boules } = useSelector((state) => state.user);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('boule');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(25);
  const [loading, setLoading] = useState(false);

  const handleGo = async () => {
    setLoading(true);
    await dispatch(getBoules());
    setLoading(false);
  };

  useEffect(() => {
    handleGo();
  }, [dispatch]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = boules?.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - boules.length) : 0;

  const filteredBoules = applySortFilter(boules, getComparator(order, orderBy), filterName);

  const isBouleNotFound = filteredBoules?.length === 0;

  return (
    <Page title="Boules block: List | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Liste des boules block"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Boules block', href: PATH_DASHBOARD.borlette?.blocage_boule },
            { name: 'Listes' }
          ]}
          action={
            handlePermission(user?.perms, 'add_bouleblock') && (
              <Button
                variant="contained"
                component={RouterLink}
                to={PATH_DASHBOARD.borlette?.add_blocage_boule}
                startIcon={<Icon icon={plusFill} />}
              >
                Ajouter Boule
              </Button>
            )
          }
        />

        <Card>
          <BouleListToolbar
            handleGo={handleGo}
            loading={loading}
            filterName={filterName}
            onFilterName={handleFilterByName}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <BouleListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={[
                    ...(user?.role === 'superuser' ? [{ id: 'company', label: 'Compagnie', alignRight: false }] : []),
                    ...TABLE_HEAD
                  ]}
                  rowCount={boules?.length}
                  numSelected={selected?.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                />
                <TableBody>
                  {(rowsPerPage > 0
                    ? filteredBoules?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    : filteredBoules
                  )?.map((ite) => {
                    const isItemSelected = selected.indexOf(ite?.id) !== -1;

                    return (
                      <TableRow
                        hover
                        key={ite?.id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        {user?.role === 'superuser' && (
                          <TableCell component="th" scope="row" padding="none">
                            <Stack direction="row" alignItems="center" spacing={2}>
                              <Avatar alt={ite?.companyName} src={ite?.company_photo} />
                              <Typography variant="subtitle2" noWrap>
                                {ite?.companyName}
                              </Typography>
                            </Stack>
                          </TableCell>
                        )}
                        <TableCell align="left">{ite?.tirage_name}</TableCell>
                        <TableCell align="left">
                          {ite?.agent_first_name} {ite?.agent_last_name}
                        </TableCell>
                        <TableCell align="left">{ite?.boule}</TableCell>
                        <TableCell align="left">{fDateTime(ite?.updated_on)}</TableCell>
                        <TableCell align="right">
                          <BouleMoreMenu bouleId={ite?.id} block="true" />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
                {isBouleNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <TablePagination
            labelRowsPerPage="Lignes par page"
            rowsPerPageOptions={[25, 50, 75, 100, { label: 'Tout', value: -1 }]}
            component="div"
            count={boules?.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}
